

export function calculate(location, values) {
    switch (location) {
        case 'd12':
            if (values.d10 && values.d11) {
                const res = values.d10 - values.d11
                // handleChange(location, res)
                return res
            }
        case 'd23':
            if (values.d21 && values.d22) {
                return values.d21 - values.d22
            }
        default:
            return ''
    }
}